import Box from "@mui/material/Box";
import Button from "@mui/material/Button";
import Modal from "@mui/material/Modal";
import * as React from "react";

const style = {
  position: "absolute",
  top: "50%",
  left: "50%",
  transform: "translate(-50%, -50%)",
  bgcolor: "background.paper",
  border: "2px solid #000",
  boxShadow: 24,
  p: 4,
};

const playlist = ["85WGN0PVyZk", "QEncrWjy4Q4", "0zCFhqs-1Ik"];
function Oeh(props) {
  const [open, setOpen] = React.useState(false);
  const [playVideoId, setPlayVideoId] = React.useState();
  const handleOpen = (videoId) => {
    console.log("kks", videoId);
    setPlayVideoId(videoId);
    setOpen(true);
  };
  const handleClose = () => setOpen(false);

  return (
    <div>
      {playlist.map((videoId) => (
        <Button onClick={() => handleOpen(videoId)} key={videoId}>
          <img
            width="600"
            height="340"
            src={`https://img.youtube.com/vi/${videoId}/0.jpg`}
            alt=""
            style={{ objectFit: "cover" }}
            videoId={videoId}
          />
        </Button>
      ))}
      <Modal
        open={open}
        onClose={handleClose}
        aria-labelledby="modal-modal-title"
        aria-describedby="modal-modal-description"
      >
        <Box sx={style}>
          <iframe
            width="1200"
            height="675"
            src={`https://www.youtube.com/embed/${playVideoId}?autoplay=1&rel=0&modestbranding=1&fs=0&controls=0`}
            title="YouTube video player"
            allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture"
            frameborder="0"
            allowFullScreen
          />
        </Box>
      </Modal>
    </div>
  );
}

export default Oeh;
